define('ghost/routes/error404', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    var Error404Route = Ember['default'].Route.extend({
        controllerName: "error",
        templateName: "error",
        titleToken: "Error",

        model: function model() {
            return {
                status: 404
            };
        }
    });

    exports['default'] = Error404Route;

});