define('ghost/routes/setup', ['exports', 'ember', 'simple-auth/configuration', 'ghost/mixins/style-body', 'ghost/mixins/loading-indicator'], function (exports, Ember, Configuration, styleBody, loadingIndicator) {

    'use strict';

    var SetupRoute = Ember['default'].Route.extend(styleBody['default'], loadingIndicator['default'], {
        titleToken: "Setup",

        classNames: ["ghost-setup"],

        // use the beforeModel hook to check to see whether or not setup has been
        // previously completed.  If it has, stop the transition into the setup page.

        beforeModel: function beforeModel() {
            var self = this;

            // If user is logged in, setup has already been completed.
            if (this.get("session").isAuthenticated) {
                this.transitionTo(Configuration['default'].routeAfterAuthentication);
                return;
            }

            // If user is not logged in, check the state of the setup process via the API
            return ic.ajax.request(this.get("ghostPaths.url").api("authentication/setup"), {
                type: "GET"
            }).then(function (result) {
                var setup = result.setup[0].status;

                if (setup) {
                    return self.transitionTo("signin");
                }
            });
        }
    });

    exports['default'] = SetupRoute;

});