define('ghost/routes/reset', ['exports', 'ember', 'simple-auth/configuration', 'ghost/mixins/style-body', 'ghost/mixins/loading-indicator'], function (exports, Ember, Configuration, styleBody, loadingIndicator) {

    'use strict';

    var ResetRoute = Ember['default'].Route.extend(styleBody['default'], loadingIndicator['default'], {
        classNames: ["ghost-reset"],

        beforeModel: function beforeModel() {
            if (this.get("session").isAuthenticated) {
                this.notifications.showWarn("You can't reset your password while you're signed in.", { delayed: true });
                this.transitionTo(Configuration['default'].routeAfterAuthentication);
            }
        },

        setupController: function setupController(controller, params) {
            controller.token = params.token;
        },

        // Clear out any sensitive information
        deactivate: function deactivate() {
            this._super();
            this.controller.clearData();
        }
    });

    exports['default'] = ResetRoute;

});