define('ghost/routes/settings/index', ['exports', 'simple-auth/mixins/authenticated-route-mixin', 'ghost/routes/mobile-index-route', 'ghost/mixins/current-user-settings', 'ghost/utils/mobile'], function (exports, AuthenticatedRouteMixin, MobileIndexRoute, CurrentUserSettings, mobileQuery) {

    'use strict';

    var SettingsIndexRoute = MobileIndexRoute['default'].extend(AuthenticatedRouteMixin['default'], CurrentUserSettings['default'], {
        titleToken: "Settings",

        // Redirect users without permission to view settings,
        // and show the settings.general route unless the user
        // is mobile
        beforeModel: function beforeModel() {
            var self = this;
            return this.get("session.user").then(this.transitionAuthor()).then(this.transitionEditor()).then(function () {
                if (!mobileQuery['default'].matches) {
                    self.transitionTo("settings.general");
                }
            });
        },

        desktopTransition: function desktopTransition() {
            this.transitionTo("settings.general");
        }
    });

    exports['default'] = SettingsIndexRoute;

});