define('ghost/initializers/authentication', ['exports', 'ember', 'simple-auth/session', 'simple-auth-oauth2/authenticators/oauth2'], function (exports, Ember, Session, OAuth2) {

    'use strict';

    var AuthenticationInitializer = {
        name: "authentication",
        before: "simple-auth",
        after: "registerTrailingLocationHistory",

        initialize: function initialize(container) {
            Session['default'].reopen({
                user: Ember['default'].computed(function () {
                    return container.lookup("store:main").find("user", "me");
                })
            });

            OAuth2['default'].reopen({
                makeRequest: function makeRequest(url, data) {
                    data.client_id = "ghost-admin";
                    return this._super(url, data);
                }
            });
        }
    };

    exports['default'] = AuthenticationInitializer;

});