define('ghost/routes/signin', ['exports', 'ember', 'simple-auth/configuration', 'ghost/mixins/style-body', 'ghost/mixins/loading-indicator'], function (exports, Ember, Configuration, styleBody, loadingIndicator) {

    'use strict';

    var SigninRoute = Ember['default'].Route.extend(styleBody['default'], loadingIndicator['default'], {
        titleToken: "Sign In",

        classNames: ["ghost-login"],

        beforeModel: function beforeModel() {
            if (this.get("session").isAuthenticated) {
                this.transitionTo(Configuration['default'].routeAfterAuthentication);
            }
        },

        model: function model() {
            return Ember['default'].Object.create({
                identification: "",
                password: ""
            });
        },

        // the deactivate hook is called after a route has been exited.
        deactivate: function deactivate() {
            this._super();

            var controller = this.controllerFor("signin");

            // clear the properties that hold the credentials when we're no longer on the signin screen
            controller.set("model.identification", "");
            controller.set("model.password", "");
        }
    });

    exports['default'] = SigninRoute;

});